import React from 'react';

import {List, message, Spin} from 'antd';
import "antd/dist/antd.css";
import reqwest from 'reqwest';
import InfiniteScroll from 'react-infinite-scroller';


const DataUrl = 'https://randomuser.me/api/?results=5&inc=name,gender,email,nat&noinfo';
/*const hotDataUrl ="";
const topSearchListUrl="";*/


class Content extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: false,
            hasMore: true,
            searchInfo:"",
            hotData: [
                {path:"",title:"想不到吧！13 个电影中想不到吧！13 个电影中的良心细节的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
                {path:"",title:"想不到吧！13 个电影中的良心细节",author:"央视网",createAt:"六小时前"},
            ],
            topSearchList:[{searchPath:"",keyword:"故宫首次晚间开放"},
                {searchPath:"",keyword:"故宫首次晚间开放"},
                {searchPath:"",keyword:"故宫晚间开放"},
                {searchPath:"",keyword:"故宫首间开放"},
                {searchPath:"",keyword:"故宫首晚间开放"},
                {searchPath:"",keyword:"故宫首次间开放"},
                {searchPath:"",keyword:"故宫首次间开放"},
                {searchPath:"",keyword:"故宫首次开放"},
                {searchPath:"",keyword:"故宫首间开放"},
                {searchPath:"",keyword:"故宫首次晚间开放"},
                {searchPath:"",keyword:"故宫首次间开放"},
                {searchPath:"",keyword:"故宫首次晚间开放"},
                {searchPath:"",keyword:"故宫首间开放"},
                {searchPath:"",keyword:"故宫首次晚间开放"},
                {searchPath:"",keyword:"故宫首次间开放"},
                {searchPath:"",keyword:"故宫首次晚间开放"},
            ],
        };
    }
    componentDidMount() {
        console.log(this.props.channel,"content组件的channelId")
        this.fetchData((res) => {
            this.setState({
                data: res.results,
            });
        });
       /* this.getData((res)=>{
            this.setState({})
        },(res)=>{
            this.setState({})
        })*/
    }

    fetchData = (callback) => {
        reqwest({
            url: DataUrl,
            type: 'json',
            method: 'get',
            contentType: 'application/json',
            success: (res) => {
                callback(res);
            },
        });
    }
   /* getData = (callback1,callback2) => {
        reqwest({
            url: hotDataUrl,
            type: 'json',
            method: 'get',
            contentType: 'application/json',
            success: (res) => {
                callback1(res);
            },
        });
        reqwest({
            url: topSearchListUrl,
            type: 'json',
            method: 'get',
            contentType: 'application/json',
            success: (res) => {
                callback2(res);
            },
        });
    }*/
    handleInfiniteOnLoad = () => {
        let data = this.state.data;
        this.setState({
            loading: true,
        });
        if (data.length > 40) {
            message.warning('新闻列表加载完了！');
            this.setState({
                hasMore: false,
                loading: false,
            });
            return;
        }
        this.fetchData((res) => {
            data = data.concat(res.results);
            this.setState({
                data,
                loading: false,
            });
        });
    }
    handleInputOnFocus= (e) =>{
        var oSearchIcon = document.getElementById('searchIcon');
        oSearchIcon.style.width=0+'px';
        e.target.style.width=166+'px';
        e.target.style.marginLeft=30+'px';
    }
    handleInputOnBlur= (e) =>{
        var oSearchIcon = document.getElementById('searchIcon');
        oSearchIcon.style.width=42+'px';
        e.target.style.width=145+'px';
        e.target.style.marginLeft=52+'px';
    }
    handleGetInputValue=(e)=>{
        this.setState({
            searchInfo : e.target.value,
        })
    }
    handleSearch= ()=>{
        const {searchInfo} = this.state;
        console.log(searchInfo,'Search------searchInfo');
        //put数据
        //跳转页面
        window.location.href=`/search/?keyword=${searchInfo}`

    }
    render() {

        return (
            <div className="content">
                <div className="main flex-block">
                    <div className="article_container flex-9">
                        <div className="content_infinite_container">
                            <InfiniteScroll
                                initialLoad={false}
                                pageStart={0}
                                loadMore={this.handleInfiniteOnLoad}
                                hasMore={!this.state.loading && this.state.hasMore}
                            >
                                <List
                                    split={false}
                                    dataSource={this.state.data}
                                    renderItem={item => (
                                        <List.Item key={item.id}>
                                            <div className="figure flex-block ">
                                                <div className="article flex-1">
                                                    <h2 className="figcaption">
                                                        <a href="www.baidu.com" title={item.name.last} alt="">元宵节将至，听习近平讲传统美德</a>
                                                    </h2>
                                                    <div className="subtitle">
                                                        <span>央视网</span>
                                                        <span>六小时前</span>
                                                    </div>
                                                </div>
                                                <a href="http://zkres2.myzaker.com/201902/5c690cd8622768633804673b_1024.jpg" alt="" className="img" style={{'backgroundImage':'url(http://zkres2.myzaker.com/201902/5c690cd8622768633804673b_1024.jpg)'}}> </a>
                                            </div>
                                        </List.Item>
                                    )}
                                >
                                    {this.state.loading && this.state.hasMore && (
                                        <div className="content_loading_container">
                                            <Spin tip="loading..."/>
                                        </div>
                                    )}
                                </List>
                            </InfiniteScroll>
                        </div>
                    </div>
                    <div id="aside" className="aside">
                        <div className="search_container">
                            <input className="search"  type="text" placeholder="搜索资讯"
                                   onFocus={this.handleInputOnFocus}
                                   onBlur={this.handleInputOnBlur}
                                   onChange={this.handleGetInputValue}
                            />
                            <div className="search_icon" id="searchIcon"></div>
                            <div className="search_btn" onClick={this.handleSearch}>搜索</div>
                            <div className="search_assist_list"></div>
                        </div>
                        <div className="channel">
                            <h3 className="channel_title">24小时热门</h3>
                            <div className="channel_list">
                                <ul className="channel_list_show">
                                    {
                                        this.state.hotData.map((item,index)=>{
                                            return (
                                                <li key={index}>
                                                    <a href={item.path} title={item.title}>{item.title}</a>
                                                    <div className="channel_subtitle">
                                                        <span>{item.author}</span>
                                                        <span>{item.createAt}</span>
                                                    </div>
                                                </li>
                                            )
                                        })
                                    }
                                </ul>
                            </div>
                            <div className="topSearch">
                                <div className="channel_title">热搜词</div>
                                {
                                    this.state.topSearchList.map((item,index)=>{
                                        return(
                                            <span key={index} style={{float:"left"}}>
                                                <a href={item.searchPath} title={item.keyword} target="_blank">{item.keyword}</a>
                                            </span>
                                        )
                                    })
                                }
                                <div style={{clear:"both"}}></div>
                            </div>
                            <div className="aboutLn">
                                <div className="channel_title">关于LightNews</div>
                                <div className="about_warp">
                                    <div className="about_box">
                                        <a href="#" target="_blank">
                                            <div className="about_bg"></div>
                                            <div className="about_title">关于LightNews</div>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}

export default Content;
